<template>
<div>
	<template v-if="!isLoading">
			<div class="found" v-if="founds">
		<div class="banner">
			<van-swipe :autoplay="3000" indicator-color="white">
				<van-swipe-item v-for="item in banners" :key="item.data.id">
					<img :src="item.data.image" />
				</van-swipe-item>
			</van-swipe>
		</div>
		<!-- 热门分类 -->
		<div class="hot">
			<div class="title" @click="$router.push('allhot')">
				<h3>热门分类</h3>
				<p class="more">
					<span>查看全部分类</span>
					<van-icon name="arrow" />
				</p>
			</div>
			<div class="content">
				<div
					class="item"
					v-for="(item, index) in 6"
					:key="index"
					@click="
						getRoute(
							'classdetail',
							'setClassificationId',
							hotClassification[index].tagId
						)
					"
				>
					<div class="cover">
						<img :src="hotClassification[index].bgPicture" alt />
						<h3 class="tags">
							<span>{{
								"#" + hotClassification[index].name
							}}</span>
						</h3>
					</div>
				</div>
			</div>
		</div>
		<!-- 专题策划 -->
		<div class="features">
			<div class="title" @click="$router.push('features')">
				<h3>{{ featuresTitle.title }}</h3>
				<p class="more">
					<span>{{ featuresTitle.rightText }}</span>
					<van-icon name="arrow" />
				</p>
			</div>
			<div class="content">
				<div
					class="item"
					v-for="(item, index) in features.data.itemList"
					:key="index"
				>
					<div
						class="cover"
						@click="
							$router.push({
								name: 'featuredetail',
								query: { id: item.data.id },
								params: { title: item.data.title }
							})
						"
					>
						<img :src="item.data.image" alt />
						<h3 class="tags">
							<span>{{ item.data.title }}</span>
						</h3>
					</div>
				</div>
			</div>
		</div>
		<div class="rank">
			<div @click="$router.push('rankpage')">
				<TitleBar :item="rankTitle"></TitleBar>
			</div>

			<div
				class="item"
				v-for="(item, index) in rank"
				:key="index"
				@click="getRoute('player', 'setVideoInfo', item)"
			>
				<VideoSmallCard :item="item"></VideoSmallCard>
			</div>
		</div>
		<div class="topics">
			<TitleBar :item="recommendedTitle"></TitleBar>
			<ul
				class="item"
				v-for="(item, index) in recommendedTopics"
				:key="index"
			>
				<RecommendedTopic :item="item"></RecommendedTopic>
			</ul>
		</div>
		<footer>
			<p>--end--</p>
		</footer>
	</div>
	</template>
	<template v-else>
		<Loading/>
	</template>
</div>
</template>

<script>
import TitleBar from "@/components/TitleBar.vue";
import VideoSmallCard from "@/components/VideoSmallCard.vue";
import RecommendedTopic from "@/components/RecommendedTopic.vue";
import Loading from "@/components/Loading.vue";
export default {
	components: {
		TitleBar,
		VideoSmallCard,
		RecommendedTopic,
		Loading
	},
	data() {
		return {
			banners: null,
			hotClassification: null,
			features: null,
			founds: null,
			rank: [],
			recommendedTopics: [],
			recommendedTitle: null,
			rankTitle: null,
			featuresTitle: null,
			scrollY:null,
			isLoading:false
		};
	},
	methods: {
		getDiscovery() {
			let discovery = JSON.parse(
				window.localStorage.getItem("discovery")
			);
			// window.console.log(discovery.text)
			if (discovery && discovery.time > Date.now()) {
				this.founds = discovery.text.data.itemList;
				this.banners = discovery.text.data.itemList[0].data.itemList;

				this.getDetails(discovery.text.data.itemList);
			} else {
				this.axios
					.get(
						"https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v7/index/tab/discovery?vc=580&vn=6.1&deviceModel=MI%20MAX%202"
					)
					.then(response => {
						// window.console.log(response);
						this.founds = response.data.itemList;
						this.banners = response.data.itemList[0].data.itemList;

						this.getDetails(response.data.itemList);
						window.localStorage.setItem(
							"discovery",
							JSON.stringify({
								text: response,
								time: Date.now() + 10 * 1000 * 60
							})
						);
					});
			}
		},
		getHot() {
			let hot = window.localStorage.getItem("hotClassification");
			if (hot) {
				this.hotClassification = JSON.parse(hot);
			} else {
				this.axios
					.get(
						`https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/categories/`
					)
					.then(response => {
						// window.console.log(response);
						this.hotClassification = response.data;
						window.localStorage.setItem(
							"hotClassification",
							JSON.stringify(response.data)
						);
					});
			}
		},
		getDetails(response) {
			response.forEach(element => {
				if (element.type == "videoSmallCard") {
					// window.console.log(element)
					this.rank.push(element);
				}
				if (element.type == "briefCard") {
					this.recommendedTopics.push(element);
				}
				if (
					element.data.dataType == "ItemCollection" &&
					element.data.header.title == "专题策划"
				) {
					this.featuresTitle = element.data.header;
				}
				if (
					element.data.dataType == "TextCardWithRightAndLeftTitle" &&
					element.data.text == "推荐主题"
				) {
					this.recommendedTitle = element;
				}
				if (
					element.data.dataType == "TextCardWithRightAndLeftTitle" &&
					element.data.text == "本周榜单"
				) {
					this.rankTitle = element;
				}
				if (
					element.data.dataType == "ItemCollection" &&
					element.data.header.title == "专题策划"
				) {
					this.features = element;
				}
			});
		},
		getRoute(path, method, id) {
			// window.console.log(id);
			this.$store.commit({
				type: method,
				aoumt: id
			});
			this.$router.push(path);
		}
	},
	created() {
		this.getDiscovery();
		this.getHot();
	},
		beforeRouteEnter(to, from, next) {
		// window.console.log(to);
		next(vm => {
			window.setTimeout(() => {
				// window.console.log(vm.scrollY);
				window.scroll(0, vm.scrollY);
				// window.console.log(window);
			}, 0);
		});
	},
	beforeRouteLeave(to, from, next) {
		this.scrollY = window.scrollY;
		next();
	}
};
</script>

<style lang="less" scoped>
.title {
	display: flex;
	justify-content: space-between;
	h3 {
		font-weight: bold;
		font-size: 20px;
		line-height: 40px;
		color: #333333;
	}
	p {
		display: flex;
		align-items: center;
		font-size: 14px;
		font-weight: bold;
		color: #4687d7;
	}
}
.undline() {
	padding-bottom: 15px;
	border-bottom: 1px solid rgb(238, 238, 238);
}
.found {
	padding: 10px;
	.banner {
		width: 100%;
		height: 180px;
		border-radius: 6px;
		overflow: hidden;
		margin-bottom: 15px;
		.van-swipe {
			.van-swipe-item {
				img {
					width: 100%;
					height: 100%;
					height: 180px;
				}
			}
		}
	}
	.hot {
		.undline;
		.title {
			display: flex;
			justify-content: space-between;
			h3 {
				font-weight: bold;
				font-size: 20px;
				line-height: 40px;
				color: #333333;
			}
			p {
				display: flex;
				align-items: center;
				font-size: 14px;
				font-weight: bold;
				color: #4687d7;
			}
		}
		.content {
			width: 100%;

			display: flex;
			justify-content: space-around;
			flex-wrap: wrap;
			.item {
				width: 33.33%;
				position: relative;
				border-radius: 5px;
				overflow: hidden;
				.cover {
					border-radius: 5px;
					overflow: hidden;
					font-size: 0;
					padding: 2px;
					// position: relative;

					img {
						width: 100%;
						height: 100%;
						border-radius: 5px;
					}
					&::after {
						content: "";
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background: rgba(0, 0, 0, 0.205);
						padding: 2px;
						background-clip: content-box;
						border-radius: 5px;
					}
				}
				h3.tags {
					font-size: 16px;
					position: absolute;
					top: 0;
					left: 0;
					z-index: 2;
					width: 100%;
					height: 100%;
					color: #fff;
					display: flex;
					justify-content: center;
					align-items: center;
					span {
						text-shadow: 0 0 2px rgba(0, 0, 0, 0.356);
					}
				}
			}
		}
	}
	.features {
		.undline;
		.content {
			width: 100%;

			display: flex;
			justify-content: space-around;
			flex-wrap: wrap;
			.item {
				width: 50%;
				position: relative;
				border-radius: 10px;
				overflow: hidden;
				.cover {
					font-size: 0;
					padding: 2px;
					// position: relative;

					img {
						width: 100%;
						height: 100%;
					}
					&::after {
						content: "";
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background: rgba(0, 0, 0, 0.315);
						padding: 2px;
						background-clip: content-box;
					}
				}
				h3.tags {
					font-size: 14px;
					position: absolute;
					top: 0;
					left: 0;
					z-index: 2;
					width: 100%;
					height: 100%;
					color: #fff;
					display: flex;
					justify-content: center;
					align-items: center;
					span {
						text-shadow: 0 0 2px rgba(0, 0, 0, 0.493);
					}
				}
			}
		}
	}
	.rank {
		.undline;
	}
	footer {
		font-family: "Niconne", cursive;
		// padding-top:10px;
		padding-bottom: 20px;
		font-size: 36px;
	}
}
</style>
