<template>
	<div class="classification-detail" v-if="headerInfo && contents">
		<div
			class="header"
			:style="{ 'background-image': `url(${headerInfo.headerImage})` }"
		>
			<nav>
				<GoBackBar :item="headerInfo.name"></GoBackBar>
			</nav>
			<div class="content">
				<h3>{{ headerInfo.name }}</h3>
				<p class="descration">{{ headerInfo.description }}</p>
				<p>
					<span>{{ headerInfo.tagFollowCount }}人关注</span>|
					<span>{{ headerInfo.lookCount }}人参与</span>
				</p>
			</div>
		</div>
		<van-list
			class="contents"
			v-model="loading"
			:finished="finished"
			finished-text="没有更多了"
			@load="onLoad"
		>
			<van-cell
				class="video-item"
				v-for="(item, index) in contents"
				:key="index"
			>
				<template v-if="item.type == 'textCard'">
					<TitleBar :item="item"></TitleBar>
				</template>
				<template v-else-if="item.type == 'followCard'">
					<div @click="getRoute('player', 'setVideoInfo', item)">
						<VideoCard :item="item"></VideoCard>
					</div>
				</template>
				<template v-else-if="item.type == 'videoSmallCard'">
					<div @click="getRoute('player', 'setVideoInfo', item)">
						<VideoSmallCard :item="item"></VideoSmallCard>
					</div>
				</template>
				<template v-if="item.type == 'autoPlayFollowCard'">
					<div @click="getRoute('player', 'setVideoInfo', item)">
						<AutoPlayFollowCard :item="item"></AutoPlayFollowCard>
					</div>
				</template>
			</van-cell>
		</van-list>
	</div>
</template>

<script>
import GoBackBar from "@/components/GoBackBar.vue";
// import { mapGetters } from "vuex";
import VideoSmallCard from "@/components/VideoSmallCard.vue";
import VideoCard from "@/components/VideoCard.vue";
import TitleBar from "@/components/TitleBar.vue";
import AutoPlayFollowCard from "@/components/AutoPlayFollowCard.vue";
export default {
	name: "ClassificationDetails",
	components: {
		GoBackBar,
		VideoSmallCard,
		VideoCard,
		TitleBar,
		AutoPlayFollowCard
	},
	// computed: {
	//   ...mapGetters(["classificationId"])
	// },
	data() {
		return {
			id: this.$store.state.classificationId,
			headerInfo: null,
			contents: [],
			nextPageUrl: null,
			nextpage: null,
			scrollY: null,
			loading: false,
			finished: false
		};
	},
	watch: {
		id() {
			this.getHeader();
		}
	},
	methods: {
		getRoute(path, method, id) {
			window.console.log(id);
			this.$store.commit({
				type: method,
				aoumt: id
			});
			this.$router.push(path);
		},
		// 触底加载更多
		onLoad() {
			this.axios
				.get(
					"https://api.kele8.cn/agent/" +
						this.nextPageUrl +
						"&vc=580&vn=6.1&deviceModel=MI%20MAX%202"
				)
				.then(response => {
					// window.console.log(response.data.itemList);
					this.loading = false;
					response.data.itemList.forEach(element => {
						this.contents.push(element);
					});
					this.nextPageUrl = response.data.nextPageUrl;
					if (!this.nextPageUrl) {
						this.finished = true;
					}
				});
		},
		getHeader() {
			this.axios
				.get(
					`https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v6/tag/index?id=${this.$store.state.classificationId}&vc=580&vn=6.1&deviceModel=MI%20MAX%202`
				)
				.then(response => {
					// window.console.log(response.data.tagInfo);
					this.headerInfo = response.data.tagInfo;
				});
		},
		getContent() {
			this.axios
				.get(
					`https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v1/tag/videos?id=${this.$store.state.classificationId}&udid=917e512b517d461cbf22eaf4dbf759ee7e15dc29&vc=580&vn=6.1&size=1080X1920&deviceModel=MI%20MAX%202&first_channel=eyepetizer_xiaomi_market&last_channel=eyepetizer_xiaomi_market&system_version_code=25`
				)
				.then(response => {
					// window.console.log(response.data.itemList);
					this.contents = response.data.itemList;
					this.nextPageUrl = response.data.nextPageUrl;
				});
		}
	},
	beforeRouteEnter(to, from, next) {
		next(vm => {
			vm.getContent();
			vm.getHeader();
			setTimeout(() => {
				window.scroll(0, vm.scrollY);
			}, 0);
		});
	},
	beforeRouteLeave(to, from, next) {
		this.scrollY = window.scrollY;
		next();
	}
};
</script>

<style lang="less" scoped>
.classification-detail {
	.header {
		width: 100%;
		height: 160px;
		background-size: cover;
		position: relative;
		&::after {
			content: "";
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background: rgba(0, 0, 0, 0.199);
		}
		nav {
			background: #fff;
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			z-index: 9;
		}
		.content {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			z-index: 2;
			color: #fff;
			padding-top: 40px;

			h3 {
				text-align: center;
				line-height: 50px;
				font-size: 20px;
				color: #fff;
				font-weight: bold;
			}
			p {
				font-size: 12px;
				line-height: 1.6em;
			}
		}
	}
	.contents {
    padding: 10px;
    .van-cell{
      padding:0;
    }
		.video-item {
			&:nth-child(2) {
				.video-card {
					border-bottom: none;
					margin-bottom: 0;
				}
			}
			&:nth-child(4) {
				border-bottom: 1px solid rgb(228, 228, 228);
				padding-bottom: 15px;
			}
		}
	}
}
</style>
