<template>
  <div>
    <template v-if="
				item.type == 'followCard' || item.type == 'autoPlayFollowCard'
			">
      <div
        class="player"
        :style="{
					'background-image': `url(http://img.kaiyanapp.com/47840a4abe62ded7d5ec48e9080afe28.jpeg?imageMogr2/quality/60/format/jpg)`
				}"
      >
        <div class="contents">
          <div class="video">
            <video
              :src="item.data.content.data.playUrl"
              :poster="item.data.content.data.cover.detail"
              controls
              autoplay
            ></video>
            <div class="control">
              <div class="bar">
                <van-icon name="arrow-left" @click="$router.go(-1)" />
              </div>
            </div>
          </div>
          <div class="info">
            <div class="title">
              <h3>{{ item.data.content.data.title }}</h3>
            </div>
            <p class="tag">
              {{
              "#" +
              item.data.content.data.category +
              " / 每日精选"
              }}
            </p>
            <p class="desration">{{ item.data.content.data.description }}</p>
          </div>
          <div class="author">
            <div class="icon">
              <img :src="item.data.content.data.author.icon" alt />
            </div>
            <div class="author-info">
              <h3 class="name">{{ item.data.content.data.author.name }}</h3>
              <p class="desration">{{ item.data.content.data.author.description }}</p>
            </div>
          </div>
          <template v-if="!isLoading">
           <div class="recommend-video">
            <h3>相关推荐</h3>
            <div class="content">
              <div
                class="item"
                v-for="(item, index) in recommendVideos"
                :key="index"
                @click="getRoute('setVideoInfo', item)"
              >
                <template v-if="item.type == 'videoSmallCard'">
                  <VideoSmallCard :item="item"></VideoSmallCard>
                </template>
              </div>
            </div>
          </div>
          </template>
          <template v-else>
            <Loading/>
          </template>
        </div>
      </div>
    </template>
    <template v-if="item.type == 'videoSmallCard' || item.type == 'video'">
      <div
        class="player"
        :style="{
					'background-image': `url(http://img.kaiyanapp.com/47840a4abe62ded7d5ec48e9080afe28.jpeg?imageMogr2/quality/60/format/jpg)`
				}"
      >
        <div class="contents">
          <div class="video">
            <video :src="item.data.playUrl" :poster="item.data.cover.detail" controls autoplay></video>
            <div class="control">
              <div class="bar">
                <van-icon name="arrow-left" @click="$router.go(-1)" />
              </div>
            </div>
          </div>
          <div class="info">
            <div class="title">
              <h3>{{ item.data.title }}</h3>
            </div>
            <p class="tag">{{ "#" + item.data.category + " / 每日精选" }}</p>
            <p class="desration">{{ item.data.description }}</p>
          </div>
          <div class="author">
            <div class="icon">
              <img :src="item.data.author.icon" alt />
            </div>
            <div class="author-info">
              <h3 class="name">{{ item.data.author.name }}</h3>
              <p class="desration">{{ item.data.author.description }}</p>
            </div>
          </div>
          <template v-if="!isLoading">
           <div class="recommend-video">
            <h3 @click="test">相关推荐</h3>
            <div class="content">
              <div class="item" v-for="(item, index) in recommendVideos" :key="index">
                <template v-if="item.type == 'videoSmallCard'">
                  <div @click="getRoute('setVideoInfo', item)">
                    <VideoSmallCard :item="item"></VideoSmallCard>
                  </div>
                </template>
              </div>
            </div>
          </div>
          </template>
             <template v-else>
            <Loading/>
          </template>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
// import { mapGetters } from 'vuex'
import VideoSmallCard from "@/components/VideoSmallCard.vue";
import Loading from "@/components/Loading.vue";
export default {
  // props:{
  //   item:Object
  // }
  components: {
    VideoSmallCard,
    Loading
  },
  data() {
    return {
      recommendVideos: [],
      id: null,
      watchRecord: [],
      isLoading:false
    };
  },
  computed: {
    // ...mapGetters([
    //   'videoInfo'
    // ])
    item() {
      return this.$store.state.videoInfo;
    }
  },
  watch: {
    item() {
      // window.console.log("刷新");
      this.getRecommendVideo();
    }
  },
  methods: {
    test() {
      window.console.log("xxxxx");
    },
    getRoute(method, id) {
      window.console.log("id");
      this.$store.commit({
        type: method,
        aoumt: id
      });
      // this.$router.push(path);
    },
    getRecommendVideo() {
      // window.clearInterval(this.id);
      this.isLoading = true
      let video = document.querySelector("video");
      let first = true;
      let watchHistory = JSON.parse(window.localStorage.getItem("watchRecord"));
      if (watchHistory) {
        this.watchRecord = watchHistory;
      }
      this.id = setInterval(() => {
        if (!video.paused && first) {
          // window.scrollTo(0, 0);
          this.watchRecord.unshift(this.item);

          window.localStorage.setItem(
            "watchRecord",
            JSON.stringify(this.watchRecord)
          );
          this.$store.commit({
            type: "syncWatchRecord",
            aoumt: this.watchRecord
          });
          first = false;
        }
        // window.console.log("xxx");
      }, 100);

      if (
        this.item.type == "followCard" ||
        this.item.type == "autoPlayFollowCard"
      ) {
        this.axios
          .get(
            `https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/video/related?id=${this.item.data.content.data.id}&vc=580&vn=6.1&deviceModel=MI%20MAX%202`
          )
          .then(response => {
            // window.console.log(response.data.itemList);
            this.recommendVideos = response.data.itemList;
             this.isLoading = false
          });
      } else {
        this.axios
          .get(
            `https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/video/related?id=${this.item.data.id}&vc=580&vn=6.1&deviceModel=MI%20MAX%202`
          )
          .then(response => {
            // window.console.log(response.data.itemList);
            this.recommendVideos = response.data.itemList;
             this.isLoading = false
          });
      }
    }
  },

  beforeRouteEnter(to, from, next) {
    window.scrollTo(0, 0);
    next(vm => {
      vm.getRecommendVideo();
    });
  },
  beforeRouteLeave(to, from, next) {
    window.clearInterval(this.id);
    next();
  }
};
</script>

<style lang="less">
.player {
  background-size: cover;
  // background-position:right;
  // filter: brightness(0.6);
  position: relative;
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(0, 0, 0, 0.2);
    filter: blur(10px);
  }
  .contents {
    position: relative;
    top: 0;
    left: 0;
    z-index: 2;
    .video {
      font-size: 0;
      position: sticky;
      top: 0;
      left: 0;
      z-index: 9;

      video {
        width: 100%;
        height: 56vw;
        object-fit: cover;
        object-position: center center;
      }
      .control {
        position: absolute;
        top: 0;
        left: 0;
        // z-index: 10;
        font-size: 14px;
        width: 100%;
        height: 30px;
        color: white;
        text-align: left;
        .van-icon {
          font-size: 28px;
        }
      }
    }
    .info {
      text-align: left;
      padding: 8px 10px;
      color: rgb(221, 221, 221);
      background: rgba(0, 0, 0, 0.2);
      .title {
        h3 {
          font-size: 14px;
          font-weight: bold;
          color: white;
        }
      }
      .tag {
        font-size: 10px;
        line-height: 3em;
      }
      .desration {
        font-size: 12px;
      }
    }
    .author {
      display: flex;
      // align-items: center;
      padding: 10px;
      // background: rgba(0, 0, 0, 0.1);
      border-bottom: 1px solid rgba(175, 175, 175, 0.623);
      .icon {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 15px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .author-info {
        flex: 1;
        text-align: left;

        .name {
          font-size: 12px;
          color: rgb(233, 233, 233);
          font-weight: bold;
        }
        .desration {
          font-size: 10px;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
          text-overflow: ellipsis;
          line-height: 2em;
          color: rgb(230, 230, 230);
        }
      }
    }
    .recommend-video {
      padding: 10px;
      h3 {
        text-align: left;
        font-size: 14px;
        font-weight: bold;
        color: rgb(221, 221, 221);
      }
      .content {
        .item {
          .video-small-card {
            padding: 8px 0;
            border-bottom: 1px solid rgba(236, 236, 236, 0.1);
            .item {
              .info {
                background: none;
                padding: 3px 0;
                h3.title {
                  color: rgb(238, 238, 238);
                  line-height: 1.2em;
                  margin-left: 0;
                }
                p {
                  color: rgb(230, 230, 230);
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
