<template>
	<div>
		<template v-if="!isLoading">
			<div class="features" v-if="features">
				<nav>
					<GoBackBar :item="title"></GoBackBar>
				</nav>
				<van-list
					class="content"
					v-model="loading"
					:finished="finished"
					finished-text="没有更多了"
					@load="onLoad"
				>
					<van-cell
						class="item"
						v-for="(item, index) in features"
						:key="index"
						@click="
							$router.push({
								name: 'featuredetail',
								query: { id: item.data.id }
							})
						"
					>
						<div class="cover">
							<img :src="item.data.image" alt />
						</div>
					</van-cell>
				</van-list>
				<!-- <div class="content">
					<div
						class="item"
						v-for="(item, index) in features"
						:key="index"
						@click="
							$router.push({
								name: 'featuredetail',
								query: { id: item.data.id }
							})
						"
					>
						<div class="cover">
							<img :src="item.data.image" alt />
						</div>
					</div>
				</div> -->
			</div>
		</template>
		<template v-else>
			<Loading />
		</template>
	</div>
</template>

<script>
import GoBackBar from "@/components/GoBackBar.vue";
import Loading from "@/components/Loading.vue";
export default {
	components: {
		GoBackBar,
		Loading
	},
	data() {
		return {
			features: null,
			title: "专题",
			nextPageUrl: null,
			nextpage: null,
			isLoading: false,
			loading: false,
			finished: false,
			scrollY:null
		};
	},
	methods: {
		onLoad() {
			this.axios
				.get(
					"https://api.kele8.cn/agent/" +
						this.nextPageUrl +
						"&vc=580&vn=6.1&deviceModel=MI%20MAX%202"
				)
				.then(response => {
					this.loading = false;
					// window.console.log(response.data.itemList);
					response.data.itemList.forEach(element => {
						this.features.push(element);
					});
					this.nextPageUrl = response.data.nextPageUrl;
					if (!this.nextPageUrl) {
						this.finished = true;
					}
				});
		},
		getContent() {
			this.isLoading = true;
			this.axios
				.get(
					"https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v3/specialTopics?vc=580&vn=6.1&deviceModel=MI%20MAX%202"
				)
				.then(response => {
					// window.console.log(response.data.itemList)
					this.features = response.data.itemList;
					this.nextPageUrl = response.data.nextPageUrl;
					this.isLoading = false;
				});
		}
	},
	created() {
		this.getContent();
	},
	beforeRouteEnter(to, from, next) {
		next(vm => {
			setTimeout(() => {
				window.scrollTo(0, vm.scrollY);
			}, 0);
		});
	},
	beforeRouteLeave(to, from, next) {
		this.scrollY = window.scrollY;
		next();
	}
};
</script>

<style lang="less" scoped>
.features {
	nav {
		width: 100%;
		position: sticky;
		top: 0;
		left: 0;
		z-index: 9;
	}
	.content {
		padding: 8px 10px;
		.van-cell {
			padding: 0;
		}
		.item {
			padding-bottom: 8px;
			border-bottom: 1px solid rgb(236, 236, 236);
			margin-bottom: 10px;
			.cover {
				width: 100%;
				height: 180px;
				border-radius: 5px;
				overflow: hidden;
				img {
					width: 100%;
					height: 100%;
				}
			}
		}
	}
}
</style>
