<template>
	<div class="recommend">
	<template v-if="!isLoading">
			<van-list
			v-model="loading"
			:finished="finished"
			finished-text="没有更多了"
			@load="onLoad"
			:immediate-check="false"
		>
			<van-cell
				v-for="(item, index) in contents"
				:key="index"
				@click="getRoute(item)"
				class="item"
			>
				<template
					v-if="
						(item.type == 'textCard' &&
							item.data.dataType ==
								'TextCardWithRightAndLeftTitle') ||
							(item.data.dataType == 'TextCard' &&
								item.data.type != 'footer2')
					"
				>
					<TitleBar :item="item"></TitleBar>
				</template>
				<template
					v-if="
						(item.type == 'textCard' &&
							item.data.dataType == 'TextCardWithTagId') ||
							item.data.type == 'footer2'
					"
				>
					<div class="footer-more">
						<span>{{ item.data.text }}</span>
						<van-icon name="arrow" />
					</div>
				</template>
				<template v-else-if="item.data.titleList">
					<DailyBestNews :item="item"></DailyBestNews>
				</template>
				<template v-if="item.type == 'followCard'">
					<VideoCard :item="item"></VideoCard>
				</template>
				<template v-if="item.type == 'videoSmallCard'">
					<VideoSmallCard :item="item"></VideoSmallCard>
				</template>
				<template v-if="item.type == 'briefCard'">
					<RecommendedTopic :item="item"></RecommendedTopic>
				</template>
			</van-cell>
		</van-list>
	</template>
	<template v-else>
		<Loading/>
	</template>

	</div>
</template>

<script>
import VideoCard from "@/components/VideoCard.vue";
import TitleBar from "@/components/TitleBar.vue";
import VideoSmallCard from "@/components/VideoSmallCard.vue";
import RecommendedTopic from "@/components/RecommendedTopic.vue";
import DailyBestNews from "@/components/DailyBestNews.vue";
import Loading from "@/components/Loading.vue";
export default {
	components: {
		VideoCard,
		TitleBar,
		VideoSmallCard,
		RecommendedTopic,
		DailyBestNews,
		Loading
	},
	data() {
		return {
			contents: null,
			loading: false,
			finished: false,
			nextPageUrl: null,
			scrollY:null,
			isLoading:false
		};
	},
	methods: {
		onLoad() {
			this.axios
				.get("https://api.kele8.cn/agent/" + this.nextPageUrl)
				.then(response => {
					this.loading = false;
					// window.console.log(response);
					response.data.itemList.forEach(element => {
						this.contents.push(element);
					});
					this.nextPageUrl = response.data.nextPageUrl;
					if (!response.data.nextPageUrl) {
						this.finished = true;
					}
				});
		},
		getContents() {
			this.isLoading = true
			this.axios
				.get(
					"https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v5/index/tab/allRec?page=0&isOldUser=true&udid=ebc6079ccfd54a5ebbe81a6f619876bad91be323&vc=580&vn=6.1&size=1080X1920&deviceModel=MI%20MAX%202&first_channel=eyepetizer_xiaomi_market&last_channel=eyepetizer_xiaomi_market&system_version_code=25"
				)
				.then(response => {
					// window.console.log(response);
					this.contents = response.data.itemList;
					this.nextPageUrl = response.data.nextPageUrl;
					this.isLoading = false
				});
		},
		getRoute(item) {
			if (item.data.text == "每日开眼精选") {
				this.$router.push("handpick");
			}
			if (item.type == "followCard" || item.type == "videoSmallCard") {
				this.$store.commit({
					type: "setVideoInfo",
					aoumt: item
				});
				this.$router.push("player");
			}
		}
	},
	created() {
		this.getContents();
	},
		beforeRouteEnter(to, from, next) {
		// window.console.log(to);
		next(vm => {
			window.setTimeout(() => {
				// window.console.log(vm.scrollY);
				window.scroll(0, vm.scrollY);
				// window.console.log(window);
			}, 0);
		});
	},
	beforeRouteLeave(to, from, next) {
		this.scrollY = window.scrollY;
		next();
	}
};
</script>

<style lang="less" scope>
.recommend {
	padding: 10px;
	padding-top: 0;
	.van-list {
		.van-cell {
			padding: 0;
		}
	}
	.item {
		.title {
			// margin-top: 10px;
		}
		.footer-more {
			text-align: left;
			display: flex;
			justify-content: flex-end;
			align-items: center;
			border-bottom: 1px solid rgb(233, 233, 233);
			padding-bottom: 10px;
			color: #4687d7;
			font-size: 14px;
			font-weight: bold;
			margin-bottom: 10px;
		}
	}
}
</style>
