<template>
	<div class="feature-detail" v-if="headerInfo && contents">
		<div class="header">
			<nav>
				<GoBackBar :item="headerInfo.brief"></GoBackBar>
			</nav>
			<div
				class="bg"
				:style="{
					'background-image': `url(${headerInfo.headerImage})`
				}"
			></div>
			<div class="content">
				<p class="title">{{ headerInfo.brief }}</p>
				<div class="desration">
					<p>{{ headerInfo.text }}</p>
				</div>
			</div>
		</div>
		
		<div class="content">
			<div
				class="item"
				v-for="(item, index) in contents"
				:key="index"
				@click="getRoute('player', 'setVideoInfo', item)"
			>
				<AutoPlayFollowCard :item="item"></AutoPlayFollowCard>
			</div>
		</div>
	</div>
</template>

<script>
import GoBackBar from "@/components/GoBackBar.vue";
import AutoPlayFollowCard from "@/components/AutoPlayFollowCard.vue";
export default {
	components: {
		GoBackBar,
		AutoPlayFollowCard
	},
	data() {
		return {
			headerInfo: null,
			contents: null,
			scrollY: null
		};
	},
	methods: {
		getRoute(path, method, id) {
			window.console.log(id);
			this.$store.commit({
				type: method,
				aoumt: id
			});
			this.$router.push(path);
		},
		getContent() {
			this.axios
				.get(
					`https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v3/lightTopics/internal/${this.$route.query.id}?vc=580&vn=6.1&deviceModel=MI%20MAX%202`
				)
				.then(response => {
					window.console.log(response);
					this.headerInfo = response.data;
					this.contents = response.data.itemList;
				});
		}
	},
	beforeRouteEnter(to, from, next) {
		next(vm => {
			vm.getContent();
			setTimeout(() => {
				window.scrollTo(0, 0);
			}, 0);
		});
	},
};
</script>

<style lang="less" scoped>
.feature-detail {
	.header {
		&::after {
			content: "";
			display: block;
			width: 100%;
			height: 3px;
			background: rgb(216, 216, 216);
		}
		nav {
			width: 100%;
			position: fixed;
			top: 0;
			left: 0;
			z-index: 10;
			background: #fff;
		}
		.bg {
			width: 100%;
			height: 160px;
			background-size: cover;
			position: relative;
			&::after {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: rgba(0, 0, 0, 0.1);
			}
		}
	}
	.content {
		text-align: left;
		position: relative;
		padding: 0 10px;
		p.title {
			font-size: 14px;
			text-align: center;
			background: #fff;
			position: absolute;
			top: -18px;
			left: 5%;
			z-index: 2;
			width: 90%;
			line-height: 36px;
			border-radius: 5px;
			font-weight: bold;
			box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.205);
		}
		.desration {
			p {
				font-size: 12px;
				padding: 10px 20px;
				padding-top: 25px;
			}
		}
	}
}
</style>
