<template>
  <div class="content" v-if="item">
    <template v-if="item.type== 'followCard'">
      <div class="video-card">
        <div class="cover">
          <img :src="item.data.content.data.cover.detail" alt />
          <span class="dt">{{translateTime(item.data.content.data.duration)}}</span>
        </div>
        <div class="info">
          <div class="author-icon">
            <img :src="item.data.header.icon" alt />
          </div>
          <div class="left">
            <p class="title">{{item.data.header.title}}</p>
            <span class="description">{{item.data.header.description}}</span>
          </div>
        </div>
      </div>
    </template>
    <template v-else-if="item.type== 'video'">
      <div class="video-card">
        <div class="cover">
          <img :src="item.data.cover.detail" alt />
          <span class="dt">{{translateTime(item.data.duration)}}</span>
        </div>
        <div class="info">
          <div class="author-icon">
            <img :src="item.data.author.icon" alt />
          </div>
          <div class="left">
            <p class="title">{{item.data.title}}</p>
            <span class="description">{{item.data.author.name+" / #"+item.data.category}}</span>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  name: "VideoCard",
  props: {
    item: Object
  },
  methods: {
    translateTime(time) {
      // window.console.log(time)
      let minute =
        parseInt(time / 60) > 10
          ? parseInt(time / 60)
          : "0" + parseInt(time / 60);
      let second =
        parseInt(time % 60) > 10
          ? parseInt(time % 60)
          : "0" + parseInt(time % 60);
      return minute + ":" + second;
    }
  }
};
</script>

<style lang="less" scoped>
.video-card {
  border-bottom: 1px solid gainsboro;
  margin-bottom: 10px;
  .cover {
    width: 100%;
    height: 200px;
    border-radius: 5px;
    position: relative;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
    }
    span.dt {
      position: absolute;
      bottom: 5px;
      right: 5px;
      background: rgba(0, 0, 0, 0.603);
      color: white;
      font-size: 12px;
      padding: 3px;
      border-radius: 3px;
    }
  }
  .info {
    padding: 8px;
    display: flex;
    .author-icon {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      box-shadow: 0 0 2px 0 rgb(122, 122, 122);
      overflow: hidden;
      margin-right: 10px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .left {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      p.title {
        font-size: 12px;
        font-weight: bold;
      }
      span.description {
        display: block;
        width: 100%;
        font-size: 10px;
        color: rgb(122, 122, 122);
        text-align: left;
      }
    }
  }
}
</style>