<template>
	<div class="hand-pick">
		<div class="banner">
			<van-swipe :autoplay="3000" indicator-color="white">
				<van-swipe-item v-for="item in banners" :key="item.data.id">
					<img :src="item.data.image" />
				</van-swipe-item>
			</van-swipe>
		</div>
		<template v-if="!isLoading">
			<div class="list" v-if="nextPageUrl">
			<van-list
				v-model="loading"
				:finished="finished"
				finished-text="没有更多了"
				@load="onLoad"
			>
				<van-cell v-for="(item, index) in itemList" :key="index">
					<template v-if="item.data.type">
						<TitleBar :item="item"></TitleBar>
					</template>
					<template v-else-if="item.data.titleList">
						<DailyBestNews :item="item"></DailyBestNews>
					</template>
					<template v-else>
						<div @click="getRoute('player', 'setVideoInfo', item)">
							<VideoCard :item="item"></VideoCard>
						</div>
					</template>
				</van-cell>
			</van-list>

		</div>
		</template>
		<template v-else>
			<Loading/>
		</template>
	</div>
</template>

<script>
// @ is an alias to /src
import VideoCard from "@/components/VideoCard.vue";
import TitleBar from "@/components/TitleBar.vue";
import DailyBestNews from "@/components/DailyBestNews.vue";
import Loading from "@/components/Loading.vue";
export default {
	components: {
		VideoCard,
		TitleBar,
		DailyBestNews,
		Loading
	},
	data() {
		return {
			banners: null,
			itemList: null,
			nextPageUrl: null,
			loading: false,
			finished: false,
			scrollY: null,
			isLoading:false
		};
	},
	methods: {
		getDiscovery() {
			this.axios
				.get(
					"https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v7/index/tab/discovery?vc=580&vn=6.1&deviceModel=MI%20MAX%202"
				)
				.then(response => {
					// window.console.log(response);
					this.banners = response.data.itemList[0].data.itemList;
				});
		},
		getDailyNews() {
			this.isLoading = true
			this.axios
				.get(
					"https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v5/index/tab/feed?vc=580&vn=6.1&deviceModel=MI%20MAX%202"
				)
				.then(response => {
					// window.console.log(response.data);
					this.itemList = response.data.itemList;
					this.nextPageUrl = response.data.nextPageUrl;
						this.isLoading = false
				});
		},
		onLoad() {
			this.axios
				.get("https://api.kele8.cn/agent/" + this.nextPageUrl)
				.then(response => {
					// window.console.log(response.data.itemList);
					this.loading = false;
					response.data.itemList.forEach(element => {
						this.itemList.push(element);
					});
					this.nextPageUrl = response.data.nextPageUrl;
				});
		},
		// 视频播放页路由跳转
		getRoute(path, method, id) {
			// window.console.log(id);
			this.$store.commit({
				type: method,
				aoumt: id
			});
			this.$router.push(path);
		}
	},
	created() {
		this.getDiscovery();
		this.getDailyNews();
	},
	
	beforeRouteEnter(to, from, next) {
		// window.console.log(to);
		next(vm => {
			window.setTimeout(() => {
				// window.console.log(vm.scrollY);
				window.scroll(0, vm.scrollY);
				// window.console.log(window);
			}, 0);
		});
	},
	beforeRouteLeave(to, from, next) {
		this.scrollY = window.scrollY;
		next();
	}
};
</script>

<style lang="less">
.hand-pick {
	padding: 10px;
	.banner {
		width: 100%;
		height: 180px;
		border-radius: 6px;
		overflow: hidden;
		margin-bottom: 15px;
		.van-swipe {
			.van-swipe-item {
				img {
					width: 100%;
					height: 100%;
					height: 180px;
				}
			}
		}
	}
	.list {
		// img{
		//   width: 100%;
		//   height: 170px;
		// }
		.van-list {
			.van-cell {
				padding: 0;
			}
		}
	}
}</style
