<template>
	<div class="search">
		<div class="search-box">
			<div class="input">
				<input
					type="text"
					placeholder="搜索视频、作者、用户及标签"
					v-model="keyWords"
					@keyup.enter="getSearchResult(keyWords)"
				/>
			</div>
			<div class="cancel" @click="cancel">取消</div>
		</div>
		<template v-if="!searchResult || !keyWords">
			<div class="befor-search">
				<div class="hot-search">
					<h3>
						热搜关键词
					</h3>
					<ul class="hotwords">
						<li
							v-for="(item, index) in hotWords"
							:key="index"
							@click="getSearchResult(item)"
						>
							{{ item }}
						</li>
					</ul>
				</div>
				<div class="search-history">
					<div class="history">
						<h3>搜索历史</h3>
						<span @click="clearHistory">清除</span>
					</div>
					<ul>
						<li
							v-for="(item, index) in searchHistory"
							:key="index"
							@click="getSearchResult(item)"
						>
							{{ item }}
						</li>
					</ul>
				</div>
			</div>
		</template>
		<template v-if="searchResult && keyWords">
			<div class="search-result">
				<van-list
					v-model="loading"
					:error.sync="error"
					error-text="请求失败，点击重新加载"
					@load="onLoad"
				>
					<van-cell
						v-for="(item, index) in searchResult"
						:key="index"
					>
						<template v-if="item.type == 'textCard'">
							<div>
								<TitleBar :item="item"> </TitleBar>
							</div>
						</template>
						<template v-if="item.type == 'briefCard'">
							<div>
								<RecommendedTopic :item="item">
								</RecommendedTopic>
							</div>
						</template>

						<template
							v-if="
								item.type == 'videoSmallCard' ||
									item.type == 'followCard'
							"
						>
							<div
								@click="
									getRoute('player', 'setVideoInfo', item)
								"
							>
								<VideoSmallCard :item="item"> </VideoSmallCard>
							</div>
						</template>
					</van-cell>
				</van-list>
				<!-- <div
					class="item"
					v-for="(item, index) in searchResult"
					:key="index"
				>
					<template v-if="item.type == 'textCard'">
						<div>
							<TitleBar :item="item"> </TitleBar>
						</div>
					</template>
					<template v-if="item.type == 'briefCard'">
						<div>
							<RecommendedTopic :item="item"> </RecommendedTopic>
						</div>
					</template>
					<template v-if="item.type == 'videoSmallCard'">
						<div>
							<VideoSmallCard :item="item"> </VideoSmallCard>
						</div>
					</template>
				</div> -->
			</div>
		</template>
	</div>
</template>

<script>
import TitleBar from "@/components/TitleBar.vue";
import VideoSmallCard from "@/components/VideoSmallCard.vue";
import RecommendedTopic from "@/components/RecommendedTopic.vue";
// import VideoCard from "@/components/VideoCard.vue";
export default {
	components: {
		TitleBar,
		VideoSmallCard,
		RecommendedTopic
		// VideoCard
	},
	data() {
		return {
			hotWords: null,
			keyWords: null,
			searchResult: null,
			nextPageUrl: null,
			error: false,
			loading: false,
			searchHistory: []
		};
	},
	watch: {
		keyWords(newValue) {
			if (newValue == "") {
				this.searchResult=null
			}
		}
	},
	methods: {
		getRoute(path, method, id) {
			window.console.log(id);
			this.$store.commit({
				type: method,
				aoumt: id
			});
			this.$router.push(path);
		},
		getHotWords() {
			this.axios
				.get(
					"https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v3/queries/hot"
				)
				.then(response => {
					// window.console.log(response);
					this.hotWords = response.data;
				});
		},
		getSearchResult(words) {
			this.keyWords = words;
			this.searchHistory.push(words);
			window.localStorage.setItem(
				"searchHistory",
				JSON.stringify(this.searchHistory)
			);
			this.axios
				.get(
					`https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v3/search?query=${words}&vc=580&vn=6.1&deviceModel=MI%20MAX%202`
				)
				.then(response => {
					// window.console.log(response);
					this.searchResult = response.data.itemList;
					this.nextPageUrl = response.data.nextPageUrl;
				});
		},
		cancel() {
			this.keyWords = null;
			this.searchResult = null;
			this.$router.go(-1);
		},
		onLoad() {
			this.axios
				.get("https://api.kele8.cn/agent/" + this.nextPageUrl)
				.then(response => {
					this.loading = false;
					// window.console.log(response)
					response.data.itemList.forEach(element => {
						this.searchResult.push(element);
					});
					this.nextPageUrl = response.data.nextPageUrl;
				})
				.catch(() => {
					this.error = true;
				});
		},
		clearHistory() {
			this.searchHistory = [];
			window.localStorage.setItem(
				"searchHistory",
				JSON.stringify(this.searchHistory)
			);
		}
	},
	created() {
		this.getHotWords();
		let history = JSON.parse(window.localStorage.getItem("searchHistory"));
		if (history) {
			this.searchHistory = history;
		}
	}
};
</script>

<style lang="less" scoped>
.search {
	padding-top: 15px;
	.search-box {
		display: flex;
		padding: 0 10px;
		.input {
			flex: 1;
			padding-right: 5px;
			input {
				width: 100%;
				height: 32px;
				padding: 2px 10px;
				background: rgb(231, 231, 231);
				outline: none;
				border: none;
				border-radius: 5px;
				font-size: 12px;
				&::placeholder {
					color: rgb(160, 160, 160);
				}
			}
		}
		.cancel {
			width: 15vw;
			text-align: center;
			line-height: 32px;
		}
	}
	.befor-search {
		padding: 10px;
		.hot-search {
			h3 {
				text-align: left;
				line-height: 36px;
				// margin-bottom: 10px;
				font-weight: bold;
			}
			ul.hotwords {
				display: flex;
				flex-wrap: wrap;
				li {
					// background:red;
					border: 1px solid rgb(226, 226, 226);
					padding: 1px 8px;
					border-radius: 5px;
					font-size: 14px;
					margin: 5px 5px;
					margin-left: 0;
					color: rgb(83, 143, 233);
				}
			}
		}
		.search-history {
			text-align: left;
			padding-top: 25px;
			padding-right: 10px;
			.history {
				display: flex;
				margin-bottom: 5px;
				justify-content: space-between;
				h3 {
					font-weight: bold;
				}
				span {
					// font-weight: bold;
					display: block;
					width: 46px;
					font-size: 14px;
					text-align: center;
				}
			}

			ul {
				display: flex;
				flex-wrap: wrap;
				li {
					border: 1px solid rgb(226, 226, 226);
					padding: 1px 8px;
					border-radius: 5px;
					font-size: 14px;
					margin: 5px 5px;
					margin-left: 0;
					color: rgb(83, 143, 233);
				}
			}
		}
	}
	.search-result {
		padding: 10px;
		.van-list {
			.van-cell {
				padding: 0;
				line-height: 1em;
			}
		}
	}
}
</style>
