<template>
  <div class="auto-paly-follow-card">
    <div class="content">
      <div class="author">
        <div class="icon">
          <img :src="item.data.header.icon" alt />
        </div>
        <div class="info">
          <h3 class="is-user-name">{{item.data.header.issuerName}}</h3>
          <p>
            <span class="time">{{item.data.header.time}}</span>
          </p>
        </div>
      </div>
      <p class="descration">{{item.data.content.data.description}}</p>
      <div class="tags">
        <span
          class="tag"
          v-for="(item, index) in item.data.content.data.tags"
          :key="index"
        >{{item.name}}</span>
      </div>
      <div class="cover">
        <img :src="item.data.content.data.cover.detail" alt />
        <span class="dt">{{translateTime(item.data.content.data.duration)}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "AutoPlayFollowCard",
  props: {
    item: Object
  },
  methods: {
    translateTime(time) {
      // window.console.log(time);
      let minute =
        parseInt(time / 60) > 10
          ? parseInt(time / 60)
          : "0" + parseInt(time / 60);
      let second =
        parseInt(time % 60) > 10
          ? parseInt(time % 60)
          : "0" + parseInt(time % 60);
      return minute + ":" + second;
    }
  }
};
</script>

<style lang="less" scoped>
img {
  width: 100%;
  height: 100%;
}
.auto-paly-follow-card {
  text-align: left;
  border-bottom: 1px solid rgb(221, 221, 221);
  padding-bottom: 15px;
  margin-top: 15px;
  .content {
    .author {
      display: flex;
      text-align: left;
      padding:3px;
      .icon {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        overflow: hidden;
        box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.301);
        img {
        }
        margin-right: 12px;
      }
      .info {
        flex: 1;
        .is-user-name {
          font-size: 12px;
          font-weight: bold;
        }
        .time {
          font-size: 12px;
          span {
            color: rgb(136, 136, 136);
          }
        }
      }
    }
    .descration {
      text-align: left;
      // height:36px;

      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 13px;
      padding:0 5px;
    }
    .tags {
      line-height: 36px;
      .tag {
        font-size: 10px;
        color: rgb(93, 155, 248);
        background: rgb(231, 231, 245);
        padding: 2px 3px;
        border-radius: 5px;
        margin-right: 5px;
      }
    }
    .cover {
      width: 100%;
      height: 200px;
      position: relative;
      border-radius: 5px;
      overflow: hidden;
      img {
      }
      span {
        position: absolute;
        bottom: 5px;
        right: 5px;
        padding: 3px;
        font-size: 12px;
        color: white;
        background: rgba(0, 0, 0, 0.473);
      }
    }
  }
}
</style>