<template>
  <div class="rank-page">
    <nav>
      <GoBackBar :item="xxx"></GoBackBar>
    </nav>
    <div class="contents">
      <van-tabs v-model="active" swipeable line-width="12px">
        <van-tab title="周排行">
          <div class="video-item" v-for="(item, index) in weekRank" :key="index" @click="getRoute('player', 'setVideoInfo', item)">
            <VideoCard :item="item"></VideoCard>
          </div>
          <footer>
            <p>--the end--</p>
          </footer>
        </van-tab>
        <van-tab title="月排行">
          <div class="video-item" v-for="(item, index) in mothRank" :key="index" @click="getRoute('player', 'setVideoInfo', item)">
            <VideoCard :item="item"></VideoCard>
          </div>
          <footer>
            <p>--the end--</p>
          </footer>
        </van-tab>
        <van-tab title="总排行">
          <div class="video-item" v-for="(item, index) in totalRank" :key="index" @click="getRoute('player', 'setVideoInfo', item)">
            <VideoCard :item="item"></VideoCard>
          </div>
          <footer>
            <p>--the end--</p>
          </footer>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import GoBackBar from "@/components/GoBackBar.vue";
import VideoCard from "@/components/VideoCard.vue";
export default {
  components: {
    VideoCard,
    GoBackBar
  },
  data() {
    return {
      xxx: "排行榜",
      active: 0,
      weekRank: null,
      mothRank: null,
      totalRank: null
    };
  },
  methods: {
    getWeekRankVideo() {
      this.axios
        .get(
          "https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/rankList/videos?strategy=weekly&start=0&num=100"
        )
        .then(response => {
          // window.console.log();
          this.weekRank = response.data.itemList;
        });
    },
    getMothRankVideo() {
      this.axios
        .get(
          "https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/rankList/videos?strategy=monthly&start=0&num=100"
        )
        .then(response => {
          // window.console.log();
          this.mothRank = response.data.itemList;
        });
    },
    getTotalRankVideo() {
      this.axios
        .get(
          "https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/rankList/videos?strategy=historical&start=0&num=100"
        )
        .then(response => {
          // window.console.log();
          this.totalRank = response.data.itemList;
        });
    },
    getRoute(path, method, id) {
      window.console.log(id);
      this.$store.commit({
        type: method,
        aoumt: id
      });
      this.$router.push(path);
    }
  },
  created() {
    this.getWeekRankVideo();
    this.getMothRankVideo();
    this.getTotalRankVideo();
  }
};
</script>

<style lang="less">
.rank-page {
  nav {
    position: sticky;
    top: 0;
    left: 0;
    z-index: 9;
    width: 100%;
    background: #fff;
    .go-back {
      border: none;
    }
  }
  .contents {
    padding: 10px;
    padding-top: 46px;
    overflow: hidden;
    width: 100vw;
    .van-tabs__wrap {
      height: 36px;
      position: fixed;
      width: 100%;
      top: 36px;
      left: 0;
      z-index: 9;
      &::after {
        border: none;
        border-bottom: 1px solid rgb(233, 233, 233);
      }
      .van-tab {
        line-height: 36px;
        font-size: 12px;
        font-weight: bold;
      }
    }
    .van-tabs__line {
      bottom: 18px;
      height: 2px;
      background-color: #444;
    }
    .van-tab__pane{
      width: 100%;
      height:100vh;
      overflow-y: scroll;
    }
    footer {
      // margin-top: 10px;
      padding: 15px 0;
      // border-top: 1px solid rgb(202, 202, 202);
      p {
        line-height: 36px;
        font-size: 26px;
        font-family: "Niconne", cursive;
      }
    }
  }
}
</style>
