<template>
  <div>
    <template v-if="!isLoading">
  <div class="all-hot" v-if="allHot">
    <nav>
      <GoBackBar :item="title"></GoBackBar>
    </nav>
    <div class="content">
      <!-- 排行&专题 -->
      <div class="rank" @click="$router.push('rankpage')">
        <img :src="allHot[0].data.image" alt />
      </div>
      <div class="re" @click="$router.push('features')">
        <img :src="allHot[1].data.image" alt />
      </div>

      <div class="panorama">
        <img :src="allHot[2].data.image" alt />
      </div>
      <div class="hot-item" v-for="(item, index) in hots" :key="index">
        <div @click="getRoute('classdetail','setClassificationId', item.tagId)">
          <img :src="item.bgPicture" alt />
        <div class="tag">
          <span>{{'#'+item.name}}</span>
        </div>
        </div>
      </div>
    </div>
    <footer>
      <p>--the end--</p>
    </footer>
  </div>
    </template>
    <template v-else>
      <Loading/>
    </template>
  </div>
</template>

<script>
import GoBackBar from "@/components/GoBackBar.vue";
import Loading from "@/components/Loading.vue";
export default {
  data() {
    return {
      title: "全部分类",
      hots: null,
      allHot: [],
      isLoading:false
    };
  },
  components: {
    GoBackBar,
    Loading
  },
  methods: {
    getRoute(path, method, id) {
      window.console.log(id);
      this.$store.commit({
        type: method,
        aoumt: id
      });
      this.$router.push(path);
    }
  },
  created() {
    this.isLoading = true
    this.hots = JSON.parse(window.localStorage.getItem("hotClassification"));
    // window.console.log(this.hots);
    this.axios
      .get(
        "https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/categories/all?udid=917e512b517d461cbf22eaf4dbf759ee7e15dc29&vc=580&vn=6.1&size=1080X1920&deviceModel=MI%20MAX%202&first_channel=eyepetizer_xiaomi_market&last_channel=eyepetizer_xiaomi_market&system_version_code=25"
      )
      .then(response => {
        // window.console.log(response.data.itemList);
        this.allHot = response.data.itemList;
        this.isLoading = false
      });
  }
};
</script>

<style lang="less">
img {
  width: 100%;
}
.all-hot {
  nav {
    background: #fff;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 9;
    .title {
      text-align: center;
    }
  }
  .content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .rank,
    .re {
      width: 50vw;
      height: 50vw;
      padding: 1px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .panorama {
      width: 100vw;
      height: 50vw;
      padding: 1px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .hot-item {
      width: 50vw;
      height: 50vw;
      padding: 1px;
      position: relative;
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.233);
      }
      img {
        width: 100%;
        height: 100%;
      }
      div.tag {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        display: flex;
        z-index: 2;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        color: white;
        // text-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
        font-weight: bold;
      }
    }
  }
  footer {
    margin-top: 10px;
    padding: 15px 0;
    border-top: 1px solid rgb(202, 202, 202);
    p {
      line-height: 36px;
      font-size: 26px;
      font-family: "Niconne", cursive;
    }
  }
}
</style>